




































































.switch-hot {
  display: flex;
  height: 88px;
  line-height: 88px;
  position: relative;
}

.hot-item {
  font-size: 30px;
  color: #666;
  width: 15vw;
  text-align: center;
  margin: 0 24px;
  font-weight: 700;
}

.hot-item.active {
  color: #ef4238;
  border-bottom: 2px solid #ef4238;
}

.search-entry {
  color: #ef4238;
  font-weight: 700;
  height: 100%;
  line-height: 90px;
  padding: 0 30px;
}

.search-entry .iconfont {
  font-size: 36px;
}

.switch-content {
  padding-bottom: 100px;
}

.title {
  padding: 0 30px;
  padding-top: 20px;
  font-size: 28px;
  color: #333;
}

.most-expected {
  position: relative;
  padding: 0 30px;
  padding-bottom: 20px;
  border-bottom: 20px solid #f5f5f5;
  .title {
    padding-left: 0;
    margin-bottom: 20px;
  }
  .expected-item {
    display: inline-block;
    width: 170px;
    overflow: hidden;
    margin-right: 20px;
  }
  .poster {
    position: relative;
    width: 170px;
    height: 230px;
    margin-bottom: 12px;
  }

  .name {
    margin-bottom: 3px;
    font-size: 24px;
    color: #333;
  }

  .data {
    font-size: 24px;
    color: #999;
  }
}
